import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Header from "../components/headers";
import {ConfirmuserAction} from '../actions/useractions'

const ConfirmUser = (props) =>{
    const [email,setEmail]=useState('');

const dispatch = useDispatch()



  
const onConfirm=()=>{
 
    function validate(IDofInput,IDofErrordiv,errormsg){
        const IDofInpu = document.getElementById(IDofInput)
        const errorDiv = document.getElementById(IDofErrordiv)
        
        
        if(IDofInpu.value !== ''){
          errorDiv.innerHTML=''
          return true
        }
        else{
          errorDiv.innerHTML = errormsg
          return false
        }
        }
        if( validate('email','emailDiv','Email is mandatory...!')){

       dispatch(ConfirmuserAction(email))
       props.history.push('/signin')
    }
    }



return(
   
        <div className="container">

<div className="row">
    
  <div className="col-md-8 offset-md-2">
    <div className="login-form">
    <Header title="Confirm Your Account"></Header>
  
        <div className="form-group">
          <label >Email address</label>
          <input id="email" onChange={(e)=>{
              setEmail(e.target.value)
          }} type="email" className="form-control"   placeholder="Enter email"/>
          <div className="errordiv" id="emailDiv"  className="form-text text-muted"></div>
        </div>
        
           
        <button onClick={onConfirm} type="submit" className="btn btn-primary">Confirm</button>
     
    </div>
  </div>
</div>
    </div>
)



}

export default ConfirmUser