import {SigninAction} from '../actions/useractions'
import { useDispatch, useSelector } from 'react-redux'
import { useEffect, useState } from 'react'
const { default: Header } = require("../components/headers")


const SigninScreen = (props)=>{
const [email,setEmail]=useState('')
const [password,setPassword]=useState('')
console.log(email)
console.log(password)

const singnindatafromserver= useSelector((store)=> store.signin)
const {error,response,loading}=singnindatafromserver


const dispatch = useDispatch()

const Signin =()=>{
  dispatch(SigninAction(email,password))

}

useEffect(() => {
  if (response && response.status === 'success') {
    sessionStorage.setItem('token', response.data.token)
    props.history.push('/dashboard')
  } else if (response && response.status === 'error') {
    alert(response.error)
  } else if (error) {
    alert(error)
  }
}, [loading, error, response])


const Signup=()=>{
  props.history.push('/signup')
}
    return(
        <div className="container">
            <div className="container fluid bgr">

<div className="row">
    
  <div className="col-md-8 offset-md-2">
    <div className="login-form">
    <Header title="Sign In"></Header>

        <div className="form-group">
          <label >Email address</label>
          <input onChange={(e)=>{
            setEmail(e.target.value)
          }} type="email" className="form-control"   placeholder="Enter email"/>
          <small  className="form-text text-muted"></small>
        </div>
        <div className="form-group">
          <label >Password</label>
          <input onChange={(e)=>{setPassword(e.target.value)}} type="password" className="form-control" id="exampleInputPassword1" placeholder="Password"/>
        </div>
        <div className="form-group form-check">
          <input type="checkbox" className="form-check-input" />
          <label className="form-check-label" >Check me out</label>
        </div>
        <button onClick={Signin} type="submit" className="btn btn-primary">Sign In</button>

        <div className="float-end">          <button onClick={Signup} type="submit" className="btn btn-success float-end">Sign Up</button>

<span className="already">     Have Account?? Click</span>
</div>
    </div>
  </div>
</div>
</div>
{loading && <div>waiting for response</div>}
        </div>
    )
     }
    
    
    
     export default SigninScreen