import { ADD_CONTACT_FAIL, ADD_CONTACT_REQUEST, ADD_CONTACT_SUCCESS, GET_CONTACT_FAIL, GET_CONTACT_REQUEST, GET_CONTACT_SUCCESS } from "../constants/contactConstants"

export const ContactbookReducer = (state={},action)=>{

    switch(action.type){
        case ADD_CONTACT_REQUEST:
        return {loading:true }

        case ADD_CONTACT_SUCCESS:
            return {loading:false, response:action.payload}

        case ADD_CONTACT_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}


export const GetcontactActionReducer = (state={},action)=>{

    switch(action.type){
        case GET_CONTACT_REQUEST:
        return {loading:true }

        case GET_CONTACT_SUCCESS:
            return {loading:true, response:action.payload}

        case GET_CONTACT_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}