import { useDispatch } from "react-redux"
import {logout} from "../actions/useractions"
import blog from "../logos/blog.png"
import contact from "../logos/contact.jpg"

const { default: Header } = require("../components/headers")
const DashBoard =(props)=>{

    const dispatch = useDispatch();

    const onlogout =()=>{
dispatch(logout())
    }

    const blogger =()=>{
       props.history.push('/blog')
            }
     const ShowContactbook =()=>{
      props.history.push('/contacts')
                     }
return(
    <div>
             <button onClick={onlogout} className="btn btn-primary float-end">Logout</button>

        <Header title="Dashboard"/>



        <div >

<img src={blog} onClick={blogger} className="blog"></img>  
      </div>

      <div  >

<img src={contact} onClick={ShowContactbook} className="blog"></img>  
      </div>
      <div >

<img  className="blog"></img>  
      </div>

    </div>

    
)

}

export default DashBoard