import Header from "../components/headers"
import {GetcontactAction} from "../actions/contactbooActions"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
const ViewContacts=()=>{


    const dispatch = useDispatch()
    useEffect(()=>{
        dispatch(GetcontactAction())
    },[])

const ViewContacts = useSelector((store)=>store.showcontacts)
const {error,response,loading}=ViewContacts



useEffect(()=>{

},[error,response,loading])




    return(
        <div>
            <Header title="Contact list"/>


<table className="table table-stripped helpme">
    <thead>
        <tr>
            <td>id</td>
            <td>First name</td>
            <td>Middle name</td>
            <td>Last Name</td>
            <td>Email</td>
            <td>Phone</td>
            <td>Alternative Phone</td>
            <td>Address</td>

        </tr>
    </thead>
    <tbody>
        {response &&
        response.data &&
        response.data.data &&
        response.data.data.length > 0 &&
        response.data.data.map((d)=>{
            return(
                <tr>
                    <td>{d.id}</td>
                    <td>{d.fname}</td>
                    <td>{d.mname}</td>
                    <td>{d.lname}</td>
                    <td>{d.email}</td>
                    <td>{d.phone}</td>
                    <td>{d.alterphone}</td>
                    <td>{d.address}</td>

                </tr>
            )
        })
        }
    </tbody>
</table>

        </div>
    )
}


export default ViewContacts