export function validate(IDofInput,IDofErrordiv,errormsg){
    const IDofInpu = document.getElementById(IDofInput)
    const errorDiv = document.getElementById(IDofErrordiv)
    if(IDofInpu.value !== ''){
      errorDiv.innerHTML=''
      return true
    }
    else{
      errorDiv.innerHTML = errormsg
      return false
    }
    }

export function AlphabetValidate(IDofInput,IDofErrordiv,errormsg){
        const IDofInpu = document.getElementById(IDofInput)
        const errorDiv = document.getElementById(IDofErrordiv)
        if( isNaN(IDofInpu.value)){
          errorDiv.innerHTML=''
          return true
        }
        else{
          errorDiv.innerHTML = errormsg
          return false
        }
        }
        
        
export function NumberValidate(IDofInput,IDofErrordiv,errormsg){
            const IDofInpu = document.getElementById(IDofInput)
            const errorDiv = document.getElementById(IDofErrordiv)
            if(! isNaN(IDofInpu.value)){
              errorDiv.innerHTML=''
              return true
            }
            else{
              errorDiv.innerHTML = errormsg
              return false
            }
            }
 export function CompareNumbers(IDofInput1,IDofInput2,IDofErrordiv,errormsg){
                const IDofInpu1 = document.getElementById(IDofInput1)
                const IDofInpu2 = document.getElementById(IDofInput2)

                const errorDiv = document.getElementById(IDofErrordiv)
                if(IDofInpu1.value == IDofInpu2.value){
                  errorDiv.innerHTML=''
                  return true
                }
                else{
                  errorDiv.innerHTML = errormsg
                  return false
                }
                }
    
    
 export function BetweenFixLengthNumbers(IDofInput,IDofErrordiv,errormsg){
    
                    const errorDiv = document.getElementById(IDofErrordiv)
                    if(IDofInput.length >= 6 && IDofInput.length <= 15 ){
                      errorDiv.innerHTML=''
                      return true
                    }
                    else{
                      errorDiv.innerHTML = errormsg
                      return false
                    }
                    }
                    
export function fixedLengthNumbers(IDofInput,IDofErrordiv,errormsg){
    
                        const errorDiv = document.getElementById(IDofErrordiv)
                        if(IDofInput.length == 10 ){
                          errorDiv.innerHTML=''
                          return true
                        }
                        else{
                          errorDiv.innerHTML = errormsg
                          return false
                        }
                        }