import {addcontactbook} from '../actions/contactbooActions'
import  Validator from 'validator'
import { useState} from 'react'
import {useDispatch} from 'react-redux'
const { default: Header } = require("../components/headers")

const AddContactScreen = (props)=>{


    const [fname,setFname]=useState('');
    const [mname,setMname]=useState('');
    const [lname,setLname]=useState('');
    const [email,setEmail]=useState('');
    const [phone,setPhone]=useState('');
    const [alterphone,setAlterphone]=useState('');
    const [address,setAddress]=useState('');

const dispatch = useDispatch()


function validate(IDofInput,IDofErrordiv,errormsg){
const IDofInpu = document.getElementById(IDofInput)
const errorDiv = document.getElementById(IDofErrordiv)


if(IDofInpu.value !== ''){
  errorDiv.innerHTML=''
  return true
}
else{
  errorDiv.innerHTML = errormsg
  return false
}
}



 
const onAdd=()=>{
    if(validate('firstname','firstnameDiv','First name is mandatory....') &&
    validate('mname','mnameDiv','middle name is mandatory....')&&
     validate('lastname','lastnameDiv','Last naem is mandatory...!')&&
     validate('email','emailDiv','Email is mandatory...!')&&
     validate('phone','phoneDiv','Phone is manadatory')&&
     
     validate('address','addressDiv','Address is required')){
     
      const emailverify = document.getElementById('emailDiv')
     const phonverify = document.getElementById('phone')
     console.log(phonverify)
      if (!Validator.isEmail(email))
         {
         emailverify.innerHTML = 'enter valid email'
         return true
         }
        else
        {
          emailverify.innerHTML = ''
          dispatch(addcontactbook(fname,mname,lname,email,phone,alterphone,address))
          props.history.push('/contactbook')

         return false
         }     
  
    }
   
   
    
  
}
const onCancel=()=>{
    props.history.push('/contctbook')
  }
  
    return(
        <div>
          
    <div className="container">

      <div className="row">
          
        <div className="col-md-8 offset-md-2">
          <div className="login-form">
          <Header title="Add contact"></Header>
          <div className="form-group">
                <label >First Name</label>
                <input id="firstname" onChange={(e)=>{
                    setFname(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your First Name"/>
                <div id="firstnameDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Middle Name</label>
                <input id="mname" onChange={(e)=>{
                    setMname(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your First Name"/>
                <div id="mnameDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Last Name</label>
                <input id="lastname" onChange={(e)=>{
                    setLname(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your Last Name"/>
                <div className="errordiv" id="lastnameDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Email address</label>
                <input id="email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} type="email" className="form-control"   placeholder="Enter email"/>
                <div className="errordiv" id="emailDiv"  className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Phone</label>
                <input id="phone" onChange={(e)=>{
                    setPhone(e.target.value)
                }} type="phone" className="form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="phoneDiv" className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Alternate Phone</label>
                <input id="phone" onChange={(e)=>{
                    setAlterphone(e.target.value)
                }} type="phone" className="form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="alternatephoneDiv" className="form-text text-muted"></div>
              </div>
              <div className="form-group">
                <label >Address</label>
                <input id="address" onChange={(e)=>{
                    setAddress(e.target.value)
                }} type="text" className="form-control"   placeholder="Enter Your Address : city/town, tal, dist, state, country "/>
                <div className="errordiv" id="addressDiv" className="form-text text-muted"></div>
              </div>
              
             
              <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" />
                <label className="form-check-label" >Check me out</label>
              </div>
              <button onClick={onAdd} type="submit" className="btn btn-primary">Submit</button>
              <button onClick={onCancel} className="btn btn-danger float-end">Cancel</button>
         <div className="float-end">
</div>
          </div>
        </div>
      </div>
    </div>

    
  
 
  
</div>





       
    )
     }
    
    
    
     export default AddContactScreen