
import {applyMiddleware, combineReducers, createStore} from 'redux'
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import { SigninReducer, signupReducer }from './reducers/userReducers'
import GetuserReducer from './reducers/userReducers'
import ConfirmuserReducer from './reducers/userReducers'
import { ContactbookReducer,GetcontactActionReducer} from './reducers/contactbookReducers'
const reducers = combineReducers({
    signup:signupReducer,
    getusers:GetuserReducer,
    confirmuser:ConfirmuserReducer,
    signin:SigninReducer,
    addcontact:ContactbookReducer,
    showcontacts:GetcontactActionReducer,
})



const store = createStore(reducers,composeWithDevTools(applyMiddleware(logger,thunk)))

export default store