export const SigninReducer = (state={},action)=>{

    switch(action.type){
        case USER_SIGNIN_REQUEST:
        return {loading:true }

        case USER_SIGNIN_SUCCESS:
            return {loading:false, response:action.payload}

        case USER_SIGNIN_FAIL:
            return {loading:false, error:action.payload}

        default:
            return state
    }

}

