import './App.css';
import {BrowserRouter as Router, Route,Switch} from 'react-router-dom'
import HomeScreen from './screens/homeScreen';
import SigninScreen from './screens/signinScreen';
import SignupScreen from './screens/signupScreen';
import Navigation from './components/navigation';
import Showuser from './screens/showuser';
import ConfirmUser from './screens/confirmuser';
import DashBoard from './screens/dashboard';
import Blog from './screens/blog';
import AddContactScreen from './screens/addcontact';
import ViewContacts from './screens/contactsView';
function App() {
  return (
    <Router>
      <div className="container"> 
      <Navigation/>
      <div className="container-float">
        <Switch>
         
            <Route path="/Home" component={HomeScreen}/>
            <Route path="/Signin" component={SigninScreen}/>
            <Route path="/signup" component={SignupScreen}/>
            <Route path="/viewusers" component={Showuser}/>
            <Route path="/confirm" component={ConfirmUser}/>
            <Route path="/dashboard" component={DashBoard}/>
            <Route path="/blog" component={Blog}/>
            <Route path="/addcontact" component={AddContactScreen}/>
            <Route path="/contacts" component={ViewContacts}/>


        
        </Switch>
        </div>
      </div>

    </Router>
  );
}

export default App;
