
const { default: Header } = require("../components/headers")

 const HomeScreen = (props)=>{

    const signup = ()=>{
props.history.push('./signup')
    }

    const signin = ()=>{
props.history.push('./signin')
    }
return(
    <div className="boddy container">

        <Header title="Home"></Header>
        

             <div className="img-wrapper">
  <div className="img-overlay">
    <button onClick={signup} className="but btn btn-primary">Sign Up</button>
    <button onClick={signin} className="but btn btn-success">Sign In</button>
  </div>
</div>
</div>
    )
 }



 export default HomeScreen