import { ADD_CONTACT_FAIL, ADD_CONTACT_REQUEST, ADD_CONTACT_SUCCESS, GET_CONTACT_FAIL, GET_CONTACT_REQUEST, GET_CONTACT_SUCCESS } from "../constants/contactConstants"
import axios  from "axios"
export const addcontactbook =(fname,mname,lname,email,phone,alterphone,address)=>{
    return (dispatch)=>{
        dispatch({
            type: ADD_CONTACT_REQUEST,

        })

        const header = {
            headers:({
                'content-type':'application/json'
            })
        }

        const url="http://18.225.37.150:2021/addcontact"

        const body = {
            fname,
            mname,
            lname,
            email,
            phone,
            alterphone,
            address
        }

        axios.post(url,body,header)
              .then((response)=>{
              
                    dispatch({
                        type:ADD_CONTACT_SUCCESS,
                        payload:response
                    })
                    try{
                        alert(response.data.error.errno === 1062 ? "You Are already in Book..!!! Double entry not allowed" : "")

                    }catch(e){
                        alert("Cheers!!!Contact Added Succesfully")
                    }
              })
            
              .catch((error)=>{
                  dispatch({
                      type:ADD_CONTACT_FAIL,
                      payload:error
                    
                  })
              
              })


    }
}




export const GetcontactAction = ()=>{
    return (dispatch)=>{
        dispatch({
            type: GET_CONTACT_REQUEST
        })

        const header = {
            headers:({
                'content-type':'application/json'
            })
        }

        const url="http://18.225.37.150:2021/getcontact"

        axios.get(url,header)
              .then((response)=>{
                dispatch({
                    type:GET_CONTACT_SUCCESS,
                    payload:response
                })
              })

              .catch((error)=>{
                  dispatch({
                      type:GET_CONTACT_FAIL,
                      payload:error
                  })
              })
    }
}