import {  USER_CONFIRM_FAIL, USER_CONFIRM_REQUEST, USER_CONFIRM_SUCCESS, USER_GET_FAIL, USER_GET_REQUEST, USER_GET_SUCCESS, USER_LOGOUT, USER_SIGNIN_FAIL, USER_SIGNIN_REQUEST, USER_SIGNIN_SUCCESS, USER_SIGNUP_FAIL, USER_SIGNUP_REQUEST, USER_SIGNUP_SUCCESS } from "../constants/userconstants"
import axios from 'axios'


export const signup =(firstname,lastname,email,phone,addres,password)=>{
    return (dispatch)=>{
        dispatch({
            type: USER_SIGNUP_REQUEST,

        })

        const header = {
            header:({
                'content-type':'application/json'
            })
        }

        const url="http://18.225.37.150:2021/signup"

        const body = {
            firstname,
            lastname,
            email,
            phone,
            addres,
            password
        }


        axios.post(url,body,header)
              .then((response)=>{
              
                    dispatch({
                        type:USER_SIGNUP_SUCCESS,
                        payload:response
                    })
                    try{
                        alert(response.data.error.errno === 1062 ? "Already registerd login or verify your account" : "")

                    }catch(e){
                        alert("login Successfull please cheak your email")
                    }

              })
            
              .catch((error)=>{
                  dispatch({
                      type:USER_SIGNUP_FAIL,
                      payload:error
                    
                  })
              
              })


    }
}

export const GetuserAction =(id,firstname,lastname,email,phone,addres,password,status)=>{
    return (dispatch)=>{
        dispatch({
            type: USER_GET_REQUEST
        })
     
        const header =({
            headers:{
                'Content-type':'application/json'
        }})


        const url = "http://18.225.37.150:2021/getuser"
       
        const body ={
            id,
            firstname,
            lastname,
            email,
            phone,
            addres,
            password,
            status
        }
     
        axios.get(url,body,header)
            .then((response)=>{
                dispatch({
                    type:USER_GET_SUCCESS,
                    payload:response
                })

           
            })
            .catch((error)=>{
                dispatch({
                    type:USER_GET_FAIL,
                    payload:error
                })
            })

    }

}




export const ConfirmuserAction =(email,status)=>{
    return (dispatch)=>{
        dispatch({
            type: USER_CONFIRM_REQUEST
                })
     
        const header =({
            headers:{
                'Content-type':'application/json'
        }})


        const url = "http://18.225.37.150:2021/confirm"
       
        const body ={
            email,
            status
        }
     
        axios.patch(url,body,header)
            .then((response)=>{
                dispatch({
                    type:USER_CONFIRM_SUCCESS,
                    payload:response


                  
                })
                alert(response.data.data.changedRows==1?"Your Account Is activated You can Login Now":"Acpunt is already acivated")
            

           
            })
            .catch((error)=>{
                dispatch({
                    type:USER_CONFIRM_FAIL,
                    payload:error
                })
           })


            
       


    }

}



export const SigninAction=(email,password)=>{

    return(dispatch)=>{
        dispatch({
            type: USER_SIGNIN_REQUEST
        })
        
        const header =({
            headers:{
                'Content-type':'application/json'
        }})


        const url = "http://18.225.37.150:2021/signin"

        const body ={
            email,
            password
        }

        axios.post(url,body,header)
        .then((response)=>{
            dispatch({
                type:USER_SIGNIN_SUCCESS,
                payload:response.data
       
            })
alert(response.data.status)
                  
        })
        .catch((error)=>{
            dispatch({
                type:USER_SIGNIN_FAIL,
                payload:error
            })
       })

    }

}

export const logout = () => {
    return (dispatch) => {
      sessionStorage.removeItem('token')
      dispatch({ type: USER_LOGOUT })
      document.location.href = '/home'
    }
  }